/**
 * breakpoint-up/down - Change color of a css property based on the currently active theme
 *
 * @param  {size}     min/max size of the screen
 *
 * @example:-
 *
 * HIDE FOR DESKTOP:
 @include breakpoint-down(small) {
    background: red;
 }
 * HIDE FOR MOBILE:
 @include breakpoint-up(medium) {
    background: blue;
 }
 *
 * if you are designing your layout with a mobile-first approach, then use min-width breakpoints and work your way up.
 * if you are designing for larger devices first, then set your default CSS as you normally would and adjust for smaller devices with the max-width approach.
*/
@media (max-width: 67.125em) {
  .collection-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.collection-cont__header {
  font-weight: 900;
  font-size: 2.5em;
  line-height: 3em;
  text-align: center;
}
.collection-cont__grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 320px);
  justify-content: center;
}
@media (max-width: 67.125em) {
  .collection-cont__grid {
    grid-template-columns: repeat(1, 320px);
  }
}
.collection-cont__grid--item {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  height: 26.1875rem;
  width: 30rem;
  background: #FBF9F9;
  border: 0.625rem solid #000000;
  border-radius: 0.625rem;
  padding: 2.5rem;
  text-decoration: none;
}
.collection-cont__grid--item:hover {
  background: rgba(14, 14, 14, 0.7);
  color: #fff;
}
.collection-cont__grid--title {
  font-weight: 800;
}
.collection-cont__grid--item:hover > .collection-cont__grid--title {
  color: #fff;
}